import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {AlertType} from "@component/validation/alert-type";

@Component({
   templateUrl: "edit-file-analytics.html",
   styleUrls: ["edit-file-analytics.scss"],
   selector: "vsan-edit-file-analytics",
})
export class EditFileAnalyticsComponent {
   AlertType: typeof AlertType = AlertType;

   @Input()
   isFileAnalyticsRuntimeSupported: boolean;

   @Input()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @Output()
   fileServiceConfigChange: EventEmitter<VsanFileServiceCommonConfig> = new EventEmitter();

   @Input()
   isEdit: boolean; // isEdit = true, when the user started the Edit workflow

   onConfigChanged(): void {
      this.fileServiceConfigChange.emit(this.fileServiceConfig);
   }
}
